body {
  font-family: 'Rubik', sans-serif;

}

img {
  max-width: 100%;
  height: auto;
}

h1 {
  font-size: 44px;
  font-weight: 700;
}

h2 {
  font-size: 34px;
  font-weight: 700;
  padding: 0;
  margin: 0;
}

h3 {
  font-size: 30px;
  font-weight: 700;
}

h4 {
  font-size: 20px;
}

h5 {
  font-size: 18px;
}

h6 {
  font-size: 16px;
}


p {
  font-size: 14px;
  color: #000000;
}

.header {
  background-color: #363636;
  color: #ffffff;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  align-items: center;
  border-radius: 27px;
  border: 1px solid #00a5e4;
  background-color: #00a5e4;
  box-shadow: 0 4px 21px rgba(0, 165, 228, 0.75);
  color: #ffffff;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  cursor: pointer;
  padding: 18px 40px;
}

.button:hover {
  border: 1px solid #00a5e4;
  background-color: #00a5e4;
  color: #fff;
}

section {
  padding: 60px 0;
}

.wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;

}

.title {
  color: #000;
  font-family: Rubik;
  font-size: 36px;
  font-weight: 700;
  line-height: 44px;
  text-transform: uppercase;
  text-align: center;
}

@media (max-width: 1160px) {
  .wrapper {
    max-width: 100%;
    padding: 0 5px;
  }
}

@media (max-width: 450px) {

  .title {
    font-size: 22px;
    line-height: 36px;
  }

  .first-section__title {
    font-size: 24px;
  }
}

@media (max-width: 600px) {
  .first-section .first-section__title {
    font-size: 24px;
  }
}